import {RootState} from "../../store/store";
import {useSelector} from "react-redux";
import {useNavigate} from "react-router-dom";
import {ChangeEvent, useEffect, useState} from "react";
import {
    Box,
    Button, Container,
    FormControl,
    InputLabel,
    MenuItem,
    Select, SelectChangeEvent, TextField
} from "@mui/material";
import {useCreateReviewMutation, useGetBooksMutation} from "../../store/reviewApi";

export const Authorized = () => {
    const token = useSelector((state: RootState) => state.auth.token)
    const userId = useSelector((state: RootState) => state.auth.userId)
    const [createReview] = useCreateReviewMutation()
    const [getBooks, getBooksResult] = useGetBooksMutation()

    const [book, setBook] = useState('');
    const [bookId, setBookId] = useState(0);
    const [reviewContent, setReviewContent] = useState('');

    const navigate = useNavigate()

    const handleBookChange = (event: SelectChangeEvent) => {
        setBook(event.target.value as string);
    };

    const handleBookIdChange = (event: any) => {
        setBookId(event.currentTarget.dataset.myValue as number);
    };

    const handleTextInputChange = (event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        setReviewContent(event.target.value as string);
    };

    useEffect(() => {
        if (!token) {
            navigate("/auth")
        } else {
            getBooks()
        }
    }, [token, getBooks, navigate])

    return (
        <Container component="main" maxWidth="xs">
            <Box
                sx={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    justifyContent: "center",
                    minHeight: "100vh"
                }}
            >
                <FormControl fullWidth>
                    <InputLabel>Books</InputLabel>
                    <Select
                        value={book}
                        label="Books"
                        onChange={handleBookChange}
                    >
                        {getBooksResult.data?.map(({id, name}, index) => (
                            <MenuItem key={index} value={name} data-my-value={id} onClick={handleBookIdChange}>
                                {name}
                            </MenuItem>
                        ))}
                    </Select>
                </FormControl>
                <Button>Choose book</Button>
                <TextField
                    margin="normal"
                    fullWidth
                    label="Review"
                    name="Review"
                    value={reviewContent}
                    autoFocus
                    onChange={handleTextInputChange}
                />
                <Button onClick={() => createReview({content: reviewContent, user_id: userId, book_id: bookId})}>Post
                    review</Button>
            </Box>
        </Container>
    );
}
